<template>
  <div id="appcontent">
    <GameWindow v-bind:user="user" v-if="ready && state == 'play'" />
    <div v-if="ready && state == 'communicating'">
      {{status}}
    </div>
    <div v-if="ready && state == 'main'"></div>
    <div v-if="ready && state == 'chooseHero'">
      <h1>Please choose your hero</h1>
      <div id="heroSelect">
        <div class="hero" v-on:click="chooseHero(0)">
          <label>Slime</label>
          <div style="background-image:url('/static/assets/creature.png')"></div>
          <p>Your everyday slime has arrived! Treat with care, for it will grow over time.</p>
        </div>
      </div>
    </div>
    <div v-if="ready" class="inheader">
      <span>{{user.address}}</span>
      <button v-on:click="logout">
        <a>Log out</a>
      </button>
    </div>
    <div class="hello" v-if="!ready">
      <div v-if="state == 'loading'">
        <pre>{{status}}</pre>
      </div>
      <div v-if="state == 'enableWeb3'">
        <pre>Please enable your Metamask</pre>
        <button v-on:click="enableWeb3">
          <a>
            Enable
          </a>
        </button>
      </div>
      <div v-if="state == 'signWeb3'">
        <pre>Access as {{user.address}}</pre>
        <button v-on:click="signWeb3">
          <a>
            Access
          </a>
        </button>
      </div>
      <div v-if="state == 'hasNoWeb3'">
        <pre>Please download Metamask</pre>
        <button>
          <a target="_blank" href="https://metamask.io">
            Download
          </a>
        </button>
      </div>
    </div>
  </div>
</template>

<script>

  import GameWindow from './GameWindow.vue'

export default {
  name: 'tdd',
  data () {
    return {
      user: false,
      status: "",
      state: "",
      ready: false
    }
  },
  components: {
    GameWindow
  },
  mounted () {
    //Check for module dependencies
    if(!window.Web3)  return this.setError("Failed to load web3", "hasNoWeb3");
    if(!window.wuf)   return this.setError("Failed to load user system");
    if(!window.PIXI)  return this.setError("Failed to load graphics");

    //If in vuejs dev mode, change port
    //Todo: dynamic
    if(window.wuf.host.indexOf(':8080')>-1) window.wuf.host = "http://localhost:3000"
    //Ready to check for user

    let token = wuf.getJWT()
    if(token) return this.tryLogin(token);
    this.tryWeb3()
  },
  methods: {
    setError (string, newState) {
      console.log(string)
      this.state = newState;
    },
    logout () {
      this.user = false;
      this.ready = false;
      wuf.setJWT(null)
      this.tryWeb3()
    },
    checkUserChoices() {
      this.state = (this.user.hero ? 'main' : 'chooseHero')
    },
    tryLogin () {
      let user = wuf.getJWT()
      wuf.api('user/' + user.address + '/profile')
        .then((profile)=>{
          if(profile.error) return this.tryWeb3()
          this.user = profile.user;
          this.ready = true;
          this.checkUserChoices()
        })
        .catch(()=>{
          this.user = false;
          this.ready = false;
        })
    },
    enableWeb3 () {
      wuf.connectWeb3().then(()=>{

      }).catch(()=>{

      })
    },
    chooseHero (heroId) {
      this.state ="communicating"
      this.status="Sending request to server..."
      wuf.api('user/' + this.user.address + '/chooseHero', this.user.address, {
        hero_id: heroId
      })
        .then(()=>{
          this.user.hero = heroId
          this.state = "communicating"
          this.status ="Success!"
          setTimeout(function(){this.status = ""; this.state = "main"}.bind(this), 500)
        })
        .catch(()=>{
          this.state = "communicating"
          this.status = "Failed to choose hero..."
          setTimeout(this.checkUserChoices,2000)
        })
    },
    signWeb3 () {
      let address = this.user.address;

      wuf.api('user/' + address)
        .then((response)=>{
          wuf.sign(address, response.challenge)
            .then((signature)=>{
              wuf.api('user/'+address+'/getjwt', address, {
                signature: signature
              })
                .then((response)=>{
                  if(response.error) {
                    //Could not get token

                  } else {
                    //Successfully got token
                    wuf.setJWT(response)
                    this.tryLogin()
                  }
                })
                .catch((error)=>{
                  //API Failed
                })
            })
            .catch(()=>{
              //Signature failed
            })
        })
        .catch(()=>{
          //API Failed
        })
    },
    tryWeb3 () {
      wuf.getWeb3()
        .then(()=>{
          this.state = "enableWeb3"
          this.getAddressLoop();
        })
        .catch(()=>{
          this.state = "hasNoWeb3"
        })
    },
    getAddressLoop () {
      if(this.ready) return;
      console.log("Loopering")
      if(wuf.hasVisibleAccount()) {
        this.state = 'signWeb3'
        this.user = {address:wuf.hasVisibleAccount()}
      } else {
        if(!web3) return this.state = "hasNoWeb3"
        this.state = "enableWeb3"
      }
      setTimeout(this.getAddressLoop, 500)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
#appcontent {
  margin-top:100px;
}
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

.hello {
  position:fixed;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  margin-top:-200px;
}

li {
  display: inline-block;
  margin: 0 10px;
}

main {
  color:#fafafa;
}

button {
  background:none;
  padding: none;
  outline:none; border:none;
  border: 2px solid #fafafa;
  border-radius:8px;
  transition:background .1s;
  cursor:pointer;
}
button a {
  transition:color .1s;
  text-decoration: none;
  padding:16px 20px;
  display:inline-block;
  box-sizing:border-box;
  font-weight:600;
}

button:hover {
  background:#fafafa;
}

button:hover a {
  color:#333333;
  font-weight:600;
}

a {
  color: #fafafa;
}

.inheader {
  position:fixed;
  top:0;
  right:0;
  padding-right:32px;
  font-family:monospace;
  z-index:2;
}

header {
  z-index:1;
  position:fixed;
  top:0;
  left:0;
  width:100%;
}

.inheader button {
  margin-left:0px;
  transform:scale(0.7,0.7);
}

.hero {
  overflow:hidden;
  border-radius:16px;
  cursor:pointer;
  max-width:300px;
  width:100%;
  float:left;
  background-color:#222222;
}

.hero label {
  position:relative;
  top: 8px;
  font-weight:400;
  font-size:25px;
}

.hero div {
  margin-top:-28px;
  background-color:rgba(0,0,0,0.2);
  width:100%;
  height:200px;
  background-position:center center;
  background-repeat:no-repeat;
}

#heroSelect {
  margin:0 auto;
  max-width:300px;
}

</style>


